  export default {
//    props:["tit"],
    props:{
      head:String, //为header 高度
      footer:String,//为footer 高度
      tit:String
    },
    data:function(){
      return {

      }
    },
    mounted:function(){
      let headerH=0;
      let footerH=0;
      let Dom=this.$refs.viewbox;
       if(this.head) {
         headerH=parseFloat(document.documentElement.style.fontSize)*parseFloat(this.head);
       }
       console.log(this)
       if(this.footer){
         footerH=parseFloat(document.documentElement.style.fontSize)*parseFloat(this.footer)
       }
//      console.log(document.documentElement.clientHeight-hradeH);
      Dom.style.height=document.documentElement.clientHeight-headerH-footerH+"px"
    }
  }



