
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
data:function(){
return {
isShow:false,
num:32
}
},
mounted:function(){
this.$pulick.setTitle(this,"我是主页")
let self=this;
self.isShow=true
document.title="我是page1的首页"
},
components:{
Tabbar:Tabbar,
TabbarItem:TabbarItem
},
beforeRouteEnter:function(to,form,next){//组件级钩子函数 进入路由时
console.log("beforeRouteEnter")
next(function(vm){//vm为当前组件实例
console.log(vm)
})
},
afterRouteEnter:function(){
console.log("afterEach")
},
beforeRouteUpdate:function(to,form,next){//同级导航切换路由触发的 二级导航
console.log("beforeRouteUpdate")

next()
},
beforeRouteLeave:function(to,form,next){//将要离开触发的 路由钩子导航
console.log("beforeRouteLeave");
next(function(){console.log(arguments)})

}
}




