import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

import master from "@/components/master.vue"
import page1  from "@/components/master/page1.vue"
import p1  from "@/components/master/p1.vue"

import master2  from "@/components/master2.vue"
import Twoa  from "@/components/master2/zhu.vue"

import master3  from "@/components/master3.vue"
import master4  from "@/components/master4.vue"
import master5  from "@/components/master5.vue"
import master6  from "@/components/master6.vue"
import master7  from "@/components/master7.vue"
import master8  from "@/components/master8.vue"
import master9  from "@/components/master9.vue"
import master10  from "@/components/master10.vue"
//import master11  from "@/components/master11.vue"
let master11=(resolve)=>{//webpack懒加载
  //return import ("@/components/about.vue")
  return require.ensure([],()=>{ //异地参数为依赖 是个数组
    resolve(require("@/components/master11.vue"))
  })
};


Vue.use(Router)
Router.prototype.goBack = function () {
  window.history.go(-1)
};
export default new Router({
  //mode:"history",
  base:"/dist/",
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/master',
      name: 'master',
      component: master,
      children:[
        {
          path: '',
          name: 'page1',
          component: page1
        },
        {
          path: 'p1',
          name: 'p1',
          component: p1
        }
      ]

    },
    {
      path:"/page2",
      name: 'master2',
      component: master2,
      children:[
        {
          path: '',
          name: 'Twoa',
          component:Twoa
        }
      ]
    },
    {
      path:"/master3",
      name: 'master3',
      component: master3
      //children:[
      //  {
      //    path: '',
      //    name: 'Twoa',
      //    component:Twoa
      //  }
      //]
    },
    {
      path:"/master4",
      name: 'master4',
      component: master4
    },
    {
      path:"/master5",
      name: 'master5',
      component: master5
    },
    {
      path:"/master6",
      name: 'master6',
      component: master6
    },
    {
      path:"/master7",
      name: 'master7',
      component: master7
    },
    {
      path:"/master8",
      name: 'master8',
      component: master8
    },
    {
      path:"/master9",
      name: 'master9',
      component: master9
    },
    {
      path:"/master10",
      name: 'master10',
      component: master10
    },
    {
      path:"/master11",
      name: 'master11',
      component: master11
    }
  ]
})



