import Vue from 'vue'
import Vuex from "vuex"
Vue.use(Vuex);

export default new Vuex.Store({
  state:{
    titleName:""
  },
  mutations:{
    MtitleName:function(state,N){
      state.titleName=N
    }
  },
  actions:{
    AtitleName({commit},N){
      commit("MtitleName",N)
    }
  },
  getters:{}
})



