
import Scroller from 'vux/src/components/scroller/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'



export default {
data:function () {
return {
n: 20,
n1: 10,
pullupEnabled: true,
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
}
}
},
mounted: function () {
this.$pulick.setTitle(this, "我是master的附页")
document.title="我是master的附页"
},
components: {
Scroller: Scroller,
Cell: Cell,
Spinner: Spinner
},
methods: {
loadMore: function () {
let self=this;
setTimeout(function () {
self.n += 10;
self.$nextTick(function(){
self.$refs.scroller.donePullup()
if (self.n >= 40) {
self.$refs.scroller.disablePullup()
console.log('No more data, Pullup disabled!')
}
})








}, 2000)
},
refresh:function () {
var self=this;
setTimeout(function() {
self.n= 20;
console.log(self)
self.$nextTick(function(){
setTimeout(function(){
console.log(self.$refs.scroller)
self.$refs.scroller.donePulldown()
self.pullupEnabled && self.$refs.scroller.enablePullup()
}, 10)
})
}, 2000)
},

loadMore1:function () {
let self=this
setTimeout(function() {
self.n1 += 10
self.$nextTick(function() {
self.$refs.scroller1.donePullup()
if (self.n1 >= 30) {
this.$refs.scroller1.disablePullup()
console.log('No more data, Pullup disabled!')
}
})
}, 2000)
}



}
}



