  export default {
    props:["ifShow","prompt"],
    data:function(){
      return {
        value1:"",
        value2:"",
        value3:"",
        value4:"",
        validate:"",
        num:0

      }
    },
    mounted:function(){
//      this.$refs.input1.focus()

    },
    watch:{
      ifShow:function(){
        let self=this;
        setTimeout(function(){
          self.$refs.input1.focus();
        },10)
      }
    },
    directives :{
      focus:{
        // 指令的定义
        inserted: function (el) {
          el.focus()
        }
      }
    },
    methods:{
      delete1:function(num){
          alert("aaaa")
      },
      validatebtn:function(){
         let num=this.value1+this.value2+this.value3+this.value4;
          if(num.length<4){
             this.$emit("receive",{code:0,message:this.prompt})
          }else{
            this.$emit("receive",{code:1,message:num})
          }
      },
      v1:function(num){
        this.inputactive(num)
//        this.$refs.input1.focus();
      },
      v2:function(num){
        this.inputactive(num)
//        this.$refs.input2.focus();
      },
      v3:function(num){
        this.inputactive(num)
//        this.$refs.input3.focus();
      },
      v4:function(num){
        this.inputactive(num)
//        this.$refs.input4.focus();
      },
      val1:function(input){
        this.$refs.input3.addEventListener("click",function(){
          this.focus()
        });

        if(this.value1==null||this.value1==""){

        }else{
          this.value1=this.value1[this.value1.length-1];
          this.$refs.input1.blur()
          this.$refs.input2.focus();
//          this.v2()
        }
      },
      val2:function(input){

        if(this.value2==null||this.value2==""){

        }else{
          this.value2=this.value2[this.value2.length-1];
          this.$refs.input2.blur()
          this.$refs.input3.click();
//          this.$refs.input3.addEventListener("click",function(){
//            alert("aaa")
//            this.focus()
//          });
        }
      },
      val3:function(input){
        if(this.value3==null||this.value3==""){

        }else{

          this.value3=this.value3[this.value3.length-1]
          this.$refs.input4.focus();
        }
      },
      val4:function(input){

        if(this.value4==null||this.value4==""){

        }else{
          this.value4=this.value4[this.value4.length-1];
          this.$refs.input4.blur();
        }
      }
    }
  }




